:root {
  --base-font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
    Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;

  --base-lightness-lightest: 95%;
  --base-lightness-lighter: 80%;
  --base-lightness-light: 65%;
  --base-lightness-default: 50%;
  --base-lightness-dark: 35%;
  --base-lightness-darker: 20%;
  --base-lightness-darkest: 10%;

  --base-saturation-highest: 100%;
  --base-saturation-higher: 80%;
  --base-saturation-high: 70%;
  --base-saturation-default: 50%;
  --base-saturation-low: 30%;
  --base-saturation-lower: 20%;
  --base-saturation-lowest: 0%;

  --base-color-primary-hue: 259;
  --base-color-primary: hsl(
    var(--base-color-primary-hue),
    var(--base-saturation-high),
    var(--base-lightness-default)
  );
  --base-color-primary-light: hsl(
    var(--base-color-primary-hue),
    var(--base-saturation-high),
    var(--base-lightness-light)
  );
  --base-color-primary-dark: hsl(
    var(--base-color-primary-hue),
    var(--base-saturation-high),
    var(--base-lightness-dark)
  );

  --base-color-secondary-hue: 258;
  --base-color-secondary: hsl(
    var(--base-color-secondary-hue),
    var(--base-saturation-high),
    var(--base-lightness-light)
  );
  --base-color-secondary-light: hsl(
    var(--base-color-secondary-hue),
    var(--base-saturation-high),
    var(--base-lightness-lighter)
  );
  --base-color-secondary-dark: hsl(
    var(--base-color-secondary-hue),
    var(--base-saturation-high),
    var(--base-lightness)
  );

  --base-color-success-hue: 130;
  --base-color-success: hsl(
    var(--base-color-success-hue),
    var(--base-saturation-high),
    var(--base-lightness-default)
  );
  --base-color-success-light: hsl(
    var(--base-color-success-hue),
    var(--base-saturation-high),
    var(--base-lightness-light)
  );
  --base-color-success-dark: hsl(
    var(--base-color-success-hue),
    var(--base-saturation-high),
    var(--base-lightness-dark)
  );

  --base-color-danger-hue: 0;
  --base-color-danger: hsl(
    var(--base-color-danger-hue),
    var(--base-saturation-high),
    var(--base-lightness-default)
  );
  --base-color-danger-light: hsl(
    var(--base-color-danger-hue),
    var(--base-saturation-high),
    var(--base-lightness-light)
  );
  --base-color-danger-dark: hsl(
    var(--base-color-danger-hue),
    var(--base-saturation-high),
    var(--base-lightness-dark)
  );

  --base-color-font: #333;
  --base-color-font-light: #333;
  --base-color-font-dark: black;

  --base-color-focus: #1f00ff;
  --base-color-focus-light: #d2daff;
  --base-color-focus-dark: darkblue;

  --base-color-white: white;
  --base-color-black: black;

  --base-color-ui-lightest: #fafafa;
  --base-color-ui-lighter: #eee;
  --base-color-ui-light: #cbcbcb;
  --base-color-ui: #9a9a9a;
  --base-color-ui-dark: #5e5e5e;
  --base-color-ui-darker: #565656;
  --base-color-ui-darkest: #333;

  --base-space-xs: 0.4rem;
  --base-space-sm: 0.8rem;
  --base-space-md: 1rem;
  --base-space-lg: 1.4rem;
  --base-space-xl: 2rem;

  --base-font-size-xs: 14px;
  --base-font-size-sm: 16px;
  --base-font-size-md: 20px;
  --base-font-size-lg: 26px;
  --base-font-size-xl: 30px;
  --base-font-size-xxl: 40px;

  --base-border-radius-sm: 2px;
  --base-border-radius-md: 4px;
  --base-border-radius-lg: 8px;
}

[theme="minimal"] {
  font-family: var(--base-font-family);
}

[theme="minimal"] base-select {
  transition: box-shadow 0.2s ease;
}

[theme="minimal"] base-select {
  --base-select-option-list-position: absolute;
  --base-select-option-list-top: 120%;
  --base-select-option-list-box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.1);
}

[theme="minimal"] base-select[multiple] base-option[selected] {
  display: none;
}

@media (max-width: 800px) {
  [theme="minimal"] base-select {
    --base-select-option-list-box-shadow: 0px -4px 20px 0px rgba(0, 0, 0, 0.1);
    --base-select-option-list-position: fixed;
    --base-select-option-list-top: calc(100% - 280px);
    --base-select-option-list-opacity: 1;
    --base-select-option-list-transition: all 0.2s ease-in-out;
  }

  [theme="minimal"] base-select[menu-is-open] {
    --base-select-option-list-top: calc(100% - 300px);
    --base-select-option-list-opacity: 1;
  }
}

[theme="minimal"] base-select::part(tag) {
  border-radius: 5px;
}

[theme="minimal"] base-select base-option {
  position: relative;
  padding-left: 30px;
}

[theme="minimal"] base-select base-option[selected]::before {
  position: absolute;
  left: 0;
  top: 50%;
  height: 8px;
  width: 2px;
  background-color: currentColor;
  content: "";
  transform: translateX(10px) translateY(-50%) rotate(-45deg);
  transform-origin: left bottom;
}

[theme="minimal"] base-select base-option[selected]::after {
  position: absolute;
  left: 0;
  top: calc(50% + 3px);
  height: 2px;
  width: 16px;
  background-color: currentColor;
  content: "";
  transform: translateX(10px) translateY(-50%) rotate(-45deg);
  transform-origin: left bottom;
}

@keyframes fade-in {
  from {
    background: red;
    top: 100%;
  }
  to {
    background: yellow;
    top: 120%;
  }
}
